  color: rgb(226 232 240 / var(--tw-text-opacity, 1));
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body *::-moz-selection{
  --tw-bg-opacity: 1;
  background-color: rgb(212 175 55 / var(--tw-bg-opacity, 1));
  --tw-text-opacity: 1;
  color: rgb(11 12 16 / var(--tw-text-opacity, 1));
}
body *::selection{
  --tw-bg-opacity: 1;
  background-color: rgb(212 175 55 / var(--tw-bg-opacity, 1));
  --tw-text-opacity: 1;
  color: rgb(11 12 16 / var(--tw-text-opacity, 1));
